@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap);
@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
.project__list{display:flex;justify-content:space-between;flex-wrap:wrap}.project__item{width:20%;height:60px;text-align:center;line-height:60px;border:1px solid #f98129;background-color:#fff}

.weather__info__list{-webkit-column-count:2;column-count:2;-moz-column-gap:30px;-webkit-column-gap:30px;grid-column-gap:30px;column-gap:30px}.weather__info__item{display:flex;border-bottom:1px dotted #acacac;padding:8px 0;page-break-inside:avoid;break-inside:avoid;-webkit-column-break-inside:avoid;page-break-inside:avoid}.weather__info__item>span{display:block}.weather__info__item .key{width:120px;text-align:center}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:16px}body{width:100%;line-height:normal;color:#333}img{max-width:100%;vertical-align:top;border:none}a{text-decoration:none;color:#333}li{list-style:none}h1,h2,h3,h4,h5{font-size:inherit;font-weight:normal}button{background:none;outline:none;border:none;cursor:pointer}p{word-break:keep-all}body{font-family:'Spoqa Han Sans', sans-serif}.f-ubuntu{font-family:'Ubutu', sans-serif}.f-ubuntu.bold{font-weight:700}.clear:after{clear:both;content:"";display:block}.d-none{display:none}.blind{height:0;width:0;line-height:0;visibility:hidden}.header{border-bottom:1px solid #f98129}.nav__menu__list{display:flex;justify-content:center}.nav__menu__anchor{display:block;height:40px;line-height:40px;margin:0 15px}.contents .wrap{margin-top:16px;background-color:#f0f0f0;padding:32px 24px}.wrap{width:980px;margin:0 auto}

